$moon: #f1f1f1;
$shadow:#E7E7E7;
$craters: #E3E4E5;
$cratersShadow: #cecece;
$numberOfStars: 1500;
.day-sky,
.night-sky {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: transparent;
    transition: all 1s linear;
}
.sun {
    transform: translate(-25%, 50%);
    left: 40%;
    bottom: -100%;
    position: absolute;
    box-sizing: border-box;
    width: 140px;
    height: 140px;
    animation: glow 4s infinite ease-in-out;
    background-color: #ffd93b;
    box-shadow: 0 0 54px 43px #ffd93b;
    border-radius: 70px;
    background-clip: content-box;
    margin-top: 0px;
    transition: all 1s linear;
    
    img {
        position: absolute;
        top: -65px;
        right: 0;
        left: 0;
        margin: auto;
        width: 28px;
        transition: all 1s linear;
    }
}
.stars {
    height: 100%;
    top: 0;
    position: absolute;
    width: 100%;
    .star {
        background-color: #FFF;
        border-radius: 50%;
        height: 1px;
        position: absolute;
        width: 1px;
    }
    @for $i from 1 through $numberOfStars {
        .star.star-#{$i} {
            top: random(3580) + px;
            left: random(3580) + px;
            //animation: random(5) + s sparkle (random(50)/-10) + s infinite;
        }
    }
}
.moon {
    background-color: $moon;
    box-shadow: inset -8px -7px 2px 0px $shadow;
    border-radius: 50%;
    left: 50%;
    height: 100px;
    position: relative;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100px;
    &::before {
        @extend .moon;
        content: ' ';
        position: absolute;
    }
}
.craters-top {
    background-color: $craters;
    border-radius: 50%;
    box-shadow: inset -3px -1px 2px 0px $cratersShadow;
    height: 15px;
    left: 20px;
    position: relative;
    top: 20px;
    width: 15px;
    &:before {
        @extend .craters-top;
        content: '';
        height: 10px;
        left: 20px;
        position: absolute;
        top: -2px;
        width: 10px;
    }
    &:after {
        @extend .craters-top;
        content: '';
        height: 8px;
        left: -10px;
        position: absolute;
        top: 15px;
        width: 8px;
    }
}
.craters-bottom {
    @extend .craters-top;
    left: 55px;
    top: 40px;
    &:before {
        @extend .craters-bottom;
        left: 15px;
        top: -10px;
    }
}
@keyframes glow {
    0% {
        margin-left: 0px;
        margin-top: 0px;
    }
    50% {
        box-shadow: 0 0 54px 30px #ffd93b;
        margin-left: 0.3px;
        margin-top: 0.3px;
    }
    100% {
        margin-left: 0px;
        margin-top: 0px;
    }
}
@keyframes sparkle {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.3;
    }
    100% {
        opacity: 1;
    }
}