.scenery {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 40px;
  overflow: hidden;
  transition: all 1s linear;
  .intro-text {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      height: 150px;
      width: 220px;
      text-align: center;
  }
}